import React, {useState,useEffect, useLayoutEffect } from 'react';
import { useLocation, useParams } from 'react-router-dom'
import CircularProgress from '@mui/material/CircularProgress';
import Box from '@mui/material/Box';
import TestPic from '../image/1_1.jpg'
import axios, { Axios } from 'axios'
import Cookies  from 'js-cookie'
import Rating from '@mui/material/Rating';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import '../css/StadiumDetail.css'
import Comments from '../comp/Comments';
import Divider from '@mui/material/Divider';
import CommentForm from '../comp/CommentForm';
import TextareaAutosize from '@mui/material/TextareaAutosize';

import Snackbar from '@mui/material/Snackbar';

export default function StadiumDetail() {

  const getStadium = ()=>{
    const token = Cookies.get('token')
    const config = {
        headers: { Authorization: `Bearer ${token}` }
    };
    axios.get(`http://localhost:3001/stadiums/select/${params.id}`,config).then((response)=>{
    setstadiumData(response.data)
    setSpinner(false)
    })
}
         
const [text, setText] = useState("")
const [rating, setRating] = useState(0)
const [stadiumid, setstadiumid] = useState(0)
const params = useParams();

const handleSubmit = ()=>{

  const token = Cookies.get('token')
    const config = {
        headers: { Authorization: `Bearer ${token}` }
    };

    axios.get(`http://localhost:3001/ratings/user/${params.id}`,config).then((response)=>{
      console.log(response.data.length)
      if(response.data.length == 0){
        const bodyParameters = {
          "userid": null,
          "stadiumid": null,
          "typeid": 1,
          "rating": null,
          "message": null   
       }
      
       bodyParameters.userid = Name
       bodyParameters.stadiumid = stadiumid
       bodyParameters.rating = rating
       bodyParameters.message = text
      
       
      
      axios.post("http://localhost:3001/ratings/insert/",bodyParameters).then((response)=>{
      
              console.log(response)
              window.location.reload(false);
      
          })

      }

      else{
       handleClick();

      }



      
    })
  
}


  const [spinner, setSpinner] = useState(false);   

  const [stadiumData, setstadiumData] = useState(["hello"]);   
  const location = useLocation();
  
  const token = Cookies.get('token')
  const [Name, setName] = useState();

  const [open, setOpen] = React.useState(false);

    const handleClick = () => {
      setOpen(true);
    };
  
    const handleClose = (event: React.SyntheticEvent | Event, reason?: string) => {
      if (reason === 'clickaway') {
        return;
      }
  
      setOpen(false);
    };

    const action = (
        <React.Fragment>
          <Button color="error" size="small" onClick={handleClose}>
            Close
          </Button>
          
          
        </React.Fragment>
      );

  useLayoutEffect(() => {
    setSpinner(true)
    setstadiumid(params.id)
    
    getStadium();

   
        const config = {
            headers: { Authorization: `Bearer ${token}` }
        };
        axios.get("http://localhost:3001/profile",config).then((response)=>{
        setName(response.data)
        })
   
    }, []);
  
  return (
    <div className='StadiumDetail'>

<Snackbar 
                open={open}
                autoHideDuration={6000}
                onClose={handleClose}
                message="Bereits bewertet!"
                action={action}
            />

        
      
      
      {spinner ?   <div className='Spinner'><Box sx={{ display: 'flex' }}>
    <CircularProgress /> 
     </Box></div>

: 
      
      
      <div className='StadiumDetailArea'>
        
        <img className='Stadium-gallery' src={'http://localhost/stadium/' + params.id + '_1.jpg'}></img>

        <p>
        <a href={stadiumData[0].plink1} target="_blank">Picture</a> by <a href={stadiumData[0].alink1} target="_blank">{stadiumData[0].author1}</a> under <a href={stadiumData[0].llink1} target="_blank">{stadiumData[0].license1}</a> 
        </p>
       
        <div className='Stadium-Detail-Textarea'></div>

         <h1>{stadiumData[0].name}</h1><br></br>
         Land: {stadiumData[0].country}<br></br>
         Region: {stadiumData[0].region}<br></br>
         Stadt: {stadiumData[0].city}<br></br>
         Eröffnung: {stadiumData[0].opening}<br></br>
         Kapazität: {stadiumData[0].capacity}

      


      
          
          
      </div>

      
    
}
<div className='StadiumDetail-divider'>
<Divider></Divider>
</div>

<div className='StadiumDetail-comment-form'>

  <Rating className='StadiumDetail-rating'
        name="half-rating"
        precision={0.5}
        size="large"
        value={rating}
        onChange={(event, newValue) => {
          setRating(newValue);
        }}
      />
  <TextareaAutosize
  onChange={(e)=>{
    setText(e.target.value)
  }}
  maxRows={4}
  aria-label="maximum height"
  placeholder="Write a comment"
  
  style={{ width: 500 }}
/>
  <div className='StadiumDetail-submit'>
  <Button onClick={handleSubmit} variant="contained">Rate</Button>
</div>
</div>

<div className='StadiumDetail-comment-section'>



<Comments id={params.id} user={Name}></Comments>
</div>  
    </div>
  )
}

