function getLink(id, author1, author2, author3, author4, author5){
  if(author1.length!==0){
    return 'http://localhost/stadium/' + id + '_1.jpg'
  }
  else if(author2.length!==0){
    return 'http://localhost/stadium/' + id + '_2.jpg'
  }
  else if(author3.length!==0){
    return 'http://localhost/stadium/' + id + '_3.jpg'
  }
  else if(author4.length!==0){
    return 'http://localhost/stadium/' + id + '_4.jpg'
  }
  else if(author5.length!==0){
    return 'http://localhost/stadium/' + id + '_5.jpg'
  }
  else{
    return 'http://localhost/NoImage.png'
  }
    
}
