
import React, {useState,useEffect, useLayoutEffect } from 'react';
import Stadiumcard from '../comp/Stadiumcard'
import '../css/stadium.css'
import axios, { Axios } from 'axios'
import Cookies  from 'js-cookie'
import CircularProgress from '@mui/material/CircularProgress';
import Box from '@mui/material/Box';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import InputBase from '@mui/material/InputBase';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import { styled, alpha } from '@mui/material/styles';

export default function Stadium() {

    const [Stadiums, setStadiums] = useState([]);
    const [spinner, setSpinner] = useState(false); 
    const [searchfielddata, setSearchfielddata] = useState("")
    const [search, setSearch] = useState(false)
    const [searchData, setSearchData] = useState([])  

    const Search = styled('div')(({ theme }) => ({
      position: 'relative',
      borderRadius: theme.shape.borderRadius,
      backgroundColor: alpha(theme.palette.common.white, 0.15),
      '&:hover': {
        backgroundColor: alpha(theme.palette.common.white, 0.25),
      },
      marginLeft: 0,
      width: '100%',
      [theme.breakpoints.up('sm')]: {
        marginLeft: theme.spacing(1),
        width: 'auto',
      },
    }));
    
    const SearchIconWrapper = styled('div')(({ theme }) => ({
      padding: theme.spacing(0, 2),
      height: '100%',
      position: 'absolute',
      pointerEvents: 'none',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
    }));
    
    const StyledInputBase = styled(InputBase)(({ theme }) => ({
      color: 'black',
      '& .MuiInputBase-input': {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(4)})`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('sm')]: {
          width: '24ch',
          '&:focus': {
            width: '48ch',
          },
        },
      },
    }));

    const getStadiums = ()=>{
        const token = Cookies.get('token')
        const config = {
            headers: { Authorization: `Bearer ${token}` }
        };

     const handleKeyPress = (event) => {
          if(event.key === 'Enter'){
            axios.get(`http://localhost:3001/stadiums/select/search/${searchfielddata}`).then((response)=>{

            if(event.target.value != null){
               setSearch(true)
            }
            else{
             setSearch(false)
            }
             
             console.log(response.data)
             
             })
          }
        }

        

        
        axios.post('http://localhost:3001/stadiums/filter', {
            country: null,
            region: null,
            city: null,
            league: null,
            openingMax: null,
            openingMin: null,
            capacityMax: null,
            capacityMin: null,
            visited: null,
            userID: null,
            order: 'name',
            direction: 'ASC'
        }).then((response)=>{
          setStadiums(response.data)
          setSpinner(false)
          })
      /*  axios.get("http://localhost:3001/stadiums/select",config).then((response)=>{
        setStadiums(response.data)
        console.log(response.data)
        setSpinner(false)
        })*/
    }

    useLayoutEffect(() => {
      setSpinner(true)
      getStadiums();
      console.log(Stadiums)
      }, []);


  return (
    <div className='stadium-container'>

<div className='stadium-searchfield'>

          
    
            <Search onKeyDown={(event)=>{
              if (event.key == "Enter") {
                axios.get(`http://localhost:3001/stadiums/select/search/${event.target.value}`).then((response)=>{

                if(event.target.value != null){
                   setSearch(true)
                }
                else{
                 setSearch(false)
                }
                
                setSearchData(response.data)
            
                 
                 })
              }
            }}>
            

            
            <SearchIconWrapper>
              <SearchIcon />
            </SearchIconWrapper>
            <StyledInputBase
              placeholder="Search…"
              inputProps={{ 'aria-label': 'search' }}
            />
          </Search>
      </div>   

  
    <div className='stadium'> 
    
    {search ? 
     searchData.map((anObjectMapped, index) => {
      return ( 
        <Stadiumcard id={anObjectMapped.id} capacity={anObjectMapped.capacity} opening={anObjectMapped.opening} city={anObjectMapped.city} region={anObjectMapped.region} country={anObjectMapped.country} title= {anObjectMapped.name} pic={getLink(anObjectMapped.id, anObjectMapped.author1, anObjectMapped.author2, anObjectMapped.author3, anObjectMapped.author4, anObjectMapped.author5)}></Stadiumcard>
  
      );
  })

    

      /*
    spinner ?   <div className='Spinner'><Box sx={{ display: 'flex' }}>
    <CircularProgress /> 
     </Box></div>*/

     

: 



      //Wenn Searchfield leer display alles, wenn wert drin dann fetch von api und durchgehen des gefilterten Arrays
      
    
    Stadiums.map((anObjectMapped, index) => {
    return ( 
      <Stadiumcard id={anObjectMapped.id} capacity={anObjectMapped.capacity} opening={anObjectMapped.opening} city={anObjectMapped.city} region={anObjectMapped.region} country={anObjectMapped.country} title= {anObjectMapped.name} pic={getLink(anObjectMapped.id, anObjectMapped.author1, anObjectMapped.author2, anObjectMapped.author3, anObjectMapped.author4, anObjectMapped.author5)}></Stadiumcard>

    );
})}

</div>

</div>
  )
}

function getLink(id, author1, author2, author3, author4, author5){
  if(author1.length!==0){
    return 'http://localhost/stadium/' + id + '_1.jpg'
  }
  else if(author2.length!==0){
    return 'http://localhost/stadium/' + id + '_2.jpg'
  }
  else if(author3.length!==0){
    return 'http://localhost/stadium/' + id + '_3.jpg'
  }
  else if(author4.length!==0){
    return 'http://localhost/stadium/' + id + '_4.jpg'
  }
  else if(author5.length!==0){
    return 'http://localhost/stadium/' + id + '_5.jpg'
  }
  else{
    return 'http://localhost/NoImage.png'
  }
    
}


